<template>
    <div rel="mainpage-analysis">
      <div id='category'></div>
      <div id='analysis'></div>
    </div>
</template>

<script>
import Highcharts from 'highcharts'
export default {
    name: 'mainpage-analysis',
    props: ['articles'],
    mounted () {
        let _ = this
        _.cloneArticles()
        _.prepareData()
        _.renderCategory()
        _.renderAnalysis()
    },
    data () {
        return {
            clonedArticles: [],
            categoryMap: new Map(),
            dateMap: new Map(),
            activeData: [],
            activeCat: []
        }
    },
    methods: {
        prepareData () {
            let _ = this
            for (let {category, date} of _.clonedArticles) {
                let temp = _.categoryMap.get(category);
                temp ? _.categoryMap.set(category, ++temp) : _.categoryMap.set(category, 1);
                temp = _.dateMap.get(date);
                temp ? _.dateMap.set(date, ++temp) : _.dateMap.set(date, 1);
            }
            for (let [date, mount] of _.dateMap) {
                _.activeCat.push(date)
                _.activeData.push([date, mount])
            }
        },
        cloneArticles () {
            let _ = this
            _.clonedArticles = Object.assign([], _.articles)
            _.clonedArticles.sort(function (a, b) {
                if (a.date < b.date) {
                    return -1
                } else if (a.date === b.date) {
                    return 0
                } else {
                    return 1
                }
            })
        },
        renderCategory () {
            let _ = this
            let options = {
                zchart: {
                    plotBackgroundColor: null,
                    plotBorderWidth: null,
                    plotShadow: false
                },
                title: {
                    text: '文章分类占比'
                },
                credits: {
                    text: 'www.dulinran.top',
                    href: 'https://www.dulinran.top',
                    enabled: false
                },
                tooltip: {
                    headerFormat: '{series.name}<br>',
                    pointFormat: '{point.name}: <b>{point.percentage:.1f}%</b>'
                },
                plotOptions: {
                    pie: {
                        allowPointSelect: true,
                        cursor: 'pointer',
                        dataLabels: {
                            enabled: true,
                            format: '<b>{point.name}</b>: {point.percentage:.1f} %',
                            style: {
                                color: (Highcharts.theme && Highcharts.theme.contrastTextColor) || 'black'
                            }
                        },
                        states: {
                            hover: {
                                enabled: false
                            }
                        },
                        slicedOffset: 20,         // 突出间距
                        point: {                  // 每个扇区是数据点对象，所以事件应该写在 point 下面
                            events: {
                                // 鼠标滑过是，突出当前扇区
                                mouseOver: function() {
                                    this.slice();
                                },
                                // 鼠标移出时，收回突出显示
                                mouseOut: function() {
                                    this.slice();
                                },
                                // 默认是点击突出，这里屏蔽掉
                                click: function() {
                                    return false;
                                }
                            }
                        }
                    }
                },
                series: [{
                    type: 'pie',
                    name: '文章分类占比',
                    data: [..._.categoryMap]
                }]
            }
            return new Highcharts.Chart('category', options);
        },
        renderAnalysis () {
            let _ = this
            let options = {
                title: {
                    text: '活跃趋势'
                },
                xAxis: {
                    categories: _.activeCat
                },
                credits: {
                    text: 'www.dulinrain.top',
                    href: 'https://www.dulinrain.top',
                    enabled: true
                },
                plotOptions: {
                    series: {
                        marker: {
                            radius: 3
                        }
                    }
                },
                yAxis: {
                    title: {
                        text: '文章数 (篇)'
                    }
                },
                series: [{
                    name: '文章数量',
                    data: _.activeData
                }]
            }
            return new Highcharts.Chart('analysis', options);
        }
    }
}
</script>

<style scoped>

</style>



